<template>
  <div class="t-rr-s-table-wrapper">
    <table class="table table-striped table-hover">
      <thead>
      <tr>
        <th scope="col">
          <Checkbox index="h1"  
                    v-model="checkAll"
                    @change="allchoiseFu(checkAll)"/>
        </th>
        <th scope="col">
          <div class="flex-center-center">
            <div class="el-overflow-white-space">
              {{$t('file_name')}}
            </div>
            <label for="fileName">
              <i class="transporter-menu_outline" />
            </label>   
             <input name="fileName" 
                    id="fileName" 
                    type="checkbox" 
                    v-model="fileName"
                    class="el-display-none"
                    @change="sortLis('file_name')">
          </div>
        </th>
        <th scope="col">
          <div class="flex-center-center">
            <div class="el-overflow-white-space">
              {{$t('version')}}
            </div>
            <label for="version">
              <i class="transporter-menu_outline" />
            </label>
             <input name="version" 
                    id="version" 
                    type="checkbox" 
                    v-model="version"
                    class="el-display-none"
                    @change="sortLis('version')">
          </div>
        </th>
        <th scope="col">
          <div class="flex-center-center">
            <div class="el-overflow-white-space">
              {{$t('size')}}
            </div>
            <label for="size">
              <i class="transporter-menu_outline" />
            </label>
             <input name="size" 
                    id="size" 
                    type="checkbox" 
                    v-model="size"
                    class="el-display-none"
                    @change="sortLis('size')">
          </div>
        </th>
        <th scope="col">
          <div class="flex-center-center">
            <div class="el-overflow-white-space">
              {{$t('date_of_change')}}
            </div>
            <label for="date_of_change">
              <i class="transporter-menu_outline" />
            </label>
             <input name="dateOfChange" 
                    id="dateOfChange" 
                    type="checkbox" 
                    v-model="dateOfChange"
                    class="el-display-none"
                    @change="sortLis('date_of_change')">
          </div>
        </th>
        <th scope="col">
          <div class="flex-center-center">
            <div class="el-overflow-white-space">
              {{$t('who_changed')}}
            </div>
             <label for="who_changed">
                <i class="transporter-menu_outline" />
             </label>
             <input name="whoChanged" 
                    id="whoChanged" 
                    type="checkbox" 
                    v-model="whoChanged"
                    class="el-display-none"
                    @change="sortLis('who_changed')">
          </div>
        </th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item , idx) in data"
          :key="idx"
          :class="{'t-rr-s-choise-item-active' : item.check}">
        <th scope="row">
          <Checkbox :index="idx"  
                    v-model="item.check"
                    @change="choiseFu(item)"/>
          <Popup :item="item"/>
        </th>
        <td>
          <i :class="[{'transporter-doc' : item.type === 'folder'}]"
             style="font-size: 24px"/>   
          {{item.name}}
        </td>
        <td>{{item.version}}</td>
        <td>{{item.size}}</td>
        <td>{{item.modified_time | date('date')}}</td>
        <td><i class="mr-10  transporter-profile"></i>{{item.modified_user_name}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Checkbox from '@/components/ui/Checkbox'
import Popup from '../../components/ui/Popup.vue'
export default {
  name: "Table",
  components: {
    Checkbox,
    Popup
  },
  data() {
    return {
      checkAll: false,
      lovingVue: false,
      fileName: false,
      version: false,
      size: false,
      whoChanged: false,
      dateOfChange: false,
    }
  },
  props: {
    data: {
      type: Array
    },
    triger: {
      type: Array
    }
  },
  methods: {
    allchoiseFu(checkAll) {
      if(checkAll) this.data.map(one=>one.check = true)
      if(!checkAll) this.data.map(one=>one.check = false)
    },
    choiseFu(item) {
      let checkFu = this.data.filter(one=>one.check)
      if(!item.check) this.checkAll = !!checkFu.length
      if(item.check && checkFu.length === this.data.length) this.checkAll = true
    },
    sortLis(column) {
      switch (column) {
        case "file_name":
          if(this.fileName) this.data.sort(function (a, b) {
            if (a.name < b.name) {
              return 1;
            }
            if (a.name > b.name) {
              return -1;
            }
            return 0;
          })
          if(!this.fileName) this.data.sort(function (a, b) {
            if (a.name > b.name) {
              return 1;
            }
            if (a.name < b.name) {
              return -1;
            }
            return 0;
          })
          console.log(this.data)
          break;
        case "version":
          console.log(this.version)
          if(this.version) this.data.sort(function (a, b) {
            if (a.version < b.version) {
              return 1;
            }
            if (a.version > b.version) {
              return -1;
            }
            return 0;
          })
          if(!this.version) this.data.sort(function (a, b) {
            if (a.version > b.version) {
              return 1;
            }
            if (a.version < b.version) {
              return -1;
            }
            return 0;
          })
          console.log(this.data)
          break;
        case "size":
        if(this.size) this.data.sort(function (a, b) {
          if (a.size < b.size) {
            return 1;
          }
          if (a.size > b.size) {
            return -1;
          }
          return 0;
        })
        if(!this.size) this.data.sort(function (a, b) {
          if (a.size > b.size) {
            return 1;
          }
          if (a.size < b.size) {
            return -1;
          }
          return 0;
        })
        break;
        case "date_of_change":
        if(this.dateOfChange) this.data.sort(function (a, b) {
          if (a.modified_time < b.modified_time) {
            return 1;
          }
          if (a.modified_time > b.modified_time) {
            return -1;
          }
          return 0;
        })
        if(!this.dateOfChange) this.data.sort(function (a, b) {
          if (a.modified_time > b.modified_time) {
            return 1;
          }
          if (a.modified_time < b.modified_time) {
            return -1;
          }
          return 0;
        })
        break;
        case "who_changed":
        if(this.whoChanged) this.data.sort(function (a, b) {
          if (a.modified_user_name < b.modified_user_name) {
            return 1;
          }
          if (a.modified_user_name > b.modified_user_name) {
            return -1;
          }
          return 0;
        })
        if(!this.whoChanged) this.data.sort(function (a, b) {
          if (a.modified_user_name > b.modified_user_name) {
            return 1;
          }
          if (a.modified_user_name < b.modified_user_name) {
            return -1;
          }
          return 0;
        })
        break;
        
        default:
          break;
      }
    }
  },
}
</script>

<style lang="scss">
@import 'src/assets/css/variables.scss';
@import 'src/assets/css/hub-table.scss';
